import React from "react";
import marked from "marked";
import "./../style/markdown.scss"


marked.setOptions({
    breaks: true
});

const renderer = new marked.Renderer();

// Make links open in a new tab
renderer.link = function(href, title, text) {
    return `<a href=${href} target="_blank">${text}</a>`;
}

// Give in-line code blocks a separate class for a cleaner aesthetic
renderer.codespan = function(codeString) {
    return `<code class="code-span">${codeString}</code>`;
}

class MarkdownPreviewer extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            markdown: placeholderText
        };
        this.handleChange = this.handleChange.bind(this);
    }

    handleChange(event) {
        this.setState({
            markdown: event.target.value
        });
    }

    render() {
        return (
            <div className="markdown-previewer">
                <nav><h1><i className="fab fa-markdown"></i> Markdown Previewer</h1></nav>
                <p className="enterMD">Enter markdown here:</p>
                <p className="previewer-top">Preview:</p>
                <div className="markdown-wrapper">
                    <div className="editor-wrapper">
                        <Editor markdown={this.state.markdown} onChange={this.handleChange} />
                    </div>
                    <p className="previewer-bottom">Preview:</p>
                    <Previewer markdown={this.state.markdown} />
                </div>
            </div>
        );
    }

}

const Editor = (props) => {
    return (
        <textarea id="editor" value={props.markdown} onChange={props.onChange} type="text" />
    );
}

const Previewer = (props) => {
    return (
        <div id="preview" dangerouslySetInnerHTML={{__html: marked(props.markdown, {renderer: renderer})}} ></div>
    );
}

const placeholderText = 
`# Heading 1
---
## Heading 2
### Heading 3

Body Text
**Bold Text**
_Italic Text_
**_Bold & Italic Text_**
~Strikethrough Text~
[Hyperlinks](https://www.freecodecamp.com)

\`\`\`
// This is a multi-line code block.

function anotherExample(firstLine, lastLine) {
  if (firstLine == '\`\`\`' && lastLine == '\`\`\`') {
    return multiLineCode;
  }
}
\`\`\`

There is separate styling for \`in-line code\` in this previewer.

> Block Quotes are denoted as separate from code blocks by the left border color.

1. List
 - Items
    * Can
1. Be
 * Indented
    - Repeatedly
    * Or
    - Not

Tables | Can     | Be  | Created
------ | ------- | --- | -------
With   | Pipes   | And | Hyphens
With   | Hyphens | And | Pipes  
With   | Pipes   | And | Hyphens
With   | Hyphens | And | Pipes  


Lastly, you can add images:
![Markdown Image](https://biosector01.com/w/images/bs01/thumb/5/51/ToaMatacover.png/487px-ToaMatacover.png)
`;

export default MarkdownPreviewer;