import React from "react";
import "./../style/pomodoro.scss";


class Pomodoro  extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentMinutes: 25,
            currentSeconds: 0,
            currentPhase: "Session",
            sessionLength: 25,
            breakLength: 5,
            countdown: false
        }

        this.incrementDecrement = this.incrementDecrement.bind(this);
        this.reset = this.reset.bind(this);
        this.startStop = this.startStop.bind(this);
        // this.countItDown = this.countItDown.bind(this);
    }


    incrementDecrement(event) {
        if (!this.state.countdown) {
            switch(event.target.id) {
                case "session-increment":
                    if (this.state.sessionLength < 60) {
                        this.setState({
                            sessionLength: this.state.sessionLength + 1,
                            currentMinutes: this.state.sessionLength + 1,
                            currentSeconds: 0
                        });
                    }
                    break;
                case "session-decrement":
                    if (this.state.sessionLength > 1) {
                        this.setState({
                            sessionLength: this.state.sessionLength - 1,
                            currentMinutes: this.state.sessionLength - 1,
                            currentSeconds: 0
                        });
                    }
                    break;
                case "break-increment":
                    if (this.state.breakLength < 60) {
                        this.setState({
                            breakLength: this.state.breakLength + 1
                        });
                    }
                    break;
                case "break-decrement":
                    if (this.state.breakLength > 1) {
                        this.setState({
                            breakLength: this.state.breakLength - 1
                        });
                    }
                    break;
                default:
                    break;
            }

        }
    }


    reset() {
        document.getElementById("beep").pause();
        document.getElementById("beep").currentTime = 0;
        clearInterval(this.interval);
        this.setState({
            currentMinutes: 25,
            currentSeconds: 0,
            currentPhase: "Session",
            sessionLength: 25,
            breakLength: 5,
            countdown: false
        })
    }

    tick() {
        if (this.state.currentSeconds > 0) {
            this.setState({
                currentSeconds: this.state.currentSeconds - 1
            });
        } else if (this.state.currentMinutes > 0) {
            this.setState({
                currentMinutes: this.state.currentMinutes - 1,
                currentSeconds: 59
            });
        } else {
            document.getElementById("beep").currentTime = 0;
            document.getElementById("beep").play();
            if (this.state.currentPhase === "Session") {
                this.setState({
                    currentPhase: "Break",
                    currentMinutes: this.state.breakLength
                });
            } else {
                this.setState({
                    currentPhase: "Session",
                    currentMinutes: this.state.sessionLength
                });
            }
        }
    }

    startStop() {
        if (this.state.countdown) {
            clearInterval(this.interval);
            this.setState({
                countdown: false
            });
        } else {
            this.setState({
                countdown: true
            });
            this.interval = setInterval(() => this.tick(), 1000);
        } 
    }

    componentWillUnmount() {
        clearInterval(this.interval);
    }

    render() {
        return (
            <div className="pomodoro-container">
                <div className="pomodoro">
                    <audio id="beep" src="https://freesound.org/data/previews/389/389523_5450487-lq.mp3" />
                    <div className="session-settings flank-display">
                        <div id="session-label" className="flank-label">Session Length</div>
                        <div className="flank-items">
                            <div id="session-decrement" className="pom-button flank-item" onClick={this.incrementDecrement}><i className="fas fa-minus" /></div>
                            <div id="session-length" className="flank-item flank-timer">{this.state.sessionLength}</div>
                            <div id="session-increment" className="pom-button flank-item" onClick={this.incrementDecrement}><i className="fas fa-plus" /></div>
                        </div>
                    </div>

                    <div className="main-display">
                        <div className="main-label">Pomodoro Clock</div>
                        <div>{this.state.timer}</div>
                        <div className="session-break-label" id="timer-label">{this.state.currentPhase}</div>
                        <Timer minutes={this.state.currentMinutes} seconds={this.state.currentSeconds} />
                        <div className="main-buttons">
                            <div id="reset" className="pom-button flank-item" onClick={this.reset}><i className="fas fa-sync-alt" /></div>
                            <div id="start_stop" className="pom-button flank-item" onClick={this.startStop}><i className="fas fa-play" /><i className="fas fa-pause" /></div>
                        </div>
                    </div>
                    
                    <div className="break-settings flank-display">
                        <div id="break-label" className="flank-label">Break Length</div>
                        <div className="flank-items">
                            <div id="break-decrement" className="pom-button flank-item" onClick={this.incrementDecrement}><i className="fas fa-minus" /></div>
                            <div id="break-length" className="flank-item flank-timer">{this.state.breakLength}</div>
                            <div id="break-increment" className="pom-button flank-item" onClick={this.incrementDecrement}><i className="fas fa-plus" /></div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


class Timer extends React.Component {
    clockFace(input) {
        if (input < 10) {
            return "0" + input;
        } else {
            return input;
        }
    }

    render() {
        return (
            <div className="main-timer" id="time-left">{this.clockFace(this.props.minutes)}:{this.clockFace(this.props.seconds)}</div>
        );
    }
}


export default Pomodoro;