import React from "react";
import { Link } from "react-router-dom";
import "./../style/home.scss";
import logo from './../logo.svg';

class Home extends React.Component {
    render() {
        return (
            <div className="home-container">
                <div className="nav-box">
                    <header className="nav-header">
                        <img src={logo} className="react-logo" alt="React logo" />
                        <h1>Will's React Playground</h1>
                        <p>Welcome!  This page was born of the need to host a few React doodles for display in my <a href="https://wbrueske.github.io/">portfolio</a> in a way that shows the media queries in actionâ€”through the use of a resizable <code>&lt;iframe&gt;</code>.
                        <br />
                        <a href="https://wbrueske.github.io/">See my full portfolio here</a>.</p>
                    </header>
                    <nav>
                        <ul className="home-link-ul">
                            <li><Link to="/random-quote-machine" className="home-link">Random Quote Machine</Link></li>
                            <li><Link to="/markdown-previewer" className="home-link">Markdown Previewer</Link></li>
                            <li><Link to="/calculator" className="home-link">JavaScript Calculator</Link></li>
                            <li><Link to="/pomodoro-clock" className="home-link">Pomodoro Clock</Link></li>
                        </ul>
                    </nav>
                </div>
            </div>
        );
    }
}

export default Home;