import React from "react";
import "./../style/calculator.scss";

const buttons = [
    {
        button: "zero",
        value: 0
    },
    {
        button: "one",
        value: 1
    },
    {
        button: "two",
        value: 2
    },
    {
        button: "three",
        value: 3
    },
    {
        button: "four",
        value: 4
    },
    {
        button: "five",
        value: 5
    },
    {
        button: "six",
        value: 6
    },
    {
        button: "seven",
        value: 7
    },
    {
        button: "eight",
        value: 8
    },
    {
        button: "nine",
        value: 9
    },
    {
        button: "decimal",
        value: "."
    },
    {
        button: "add",
        value: "+"
    },
    {
        button: "subtract",
        value: "-"
    },
    {
        button: "multiply",
        value: "*"
    },
    {
        button: "divide",
        value: "/"
    },
    {
        button: "equals",
        value: "="
    },
    {
        button: "clear",
        value: "C"
    }
];

class Calculator extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            display: "0",
            evaluated: false
        }
        this.handleClick = this.handleClick.bind(this);
    }


    handleClick(event) {

        let tempDisplay = this.state.display;
        let lastCharacter = tempDisplay.split("")[tempDisplay.split("").length - 1];
        let secondLast = tempDisplay.split("")[tempDisplay.split("").length - 2];

        if (this.state.evaluated === false) {
            switch(event.target.id) {
                case "clear":
                    this.setState({
                        display: "0"
                    });
                    break;

                case "equals":
                    // Break if a solve is attempted when the equation ends in an operator.
                    if (lastCharacter === "+" |
                        lastCharacter === "-" |
                        lastCharacter === "*" |
                        lastCharacter === "/") {
                            break;
                    } else {
                        this.setState({
                            display: eval(this.state.display).toString(),
                            evaluated: true
                        });
                        break;
                    }

                // math logic fun times
                case "add":
                case "multiply":
                case "divide":
                    if (tempDisplay === "0") {
                        this.setState({
                            display: tempDisplay + event.target.innerHTML
                        });
                    } else {
                        // Check to see if there's a math operator already; replace if there is.
                        if (lastCharacter === "+" |
                            lastCharacter === "*" |
                            lastCharacter === "/") {
                                this.setState({
                                    // Generate a new array sliced from the original, minus the last character.
                                    display: [...tempDisplay.split("").slice(0, tempDisplay.split("").length - 1)].join("") + event.target.innerHTML
                                });
                        // Weirdness for the minus operator to allow for other operations on negative numbers.
                        // If there is already an operator and a minus, replace both...
                        } else if (lastCharacter === "-") {
                            if (secondLast === "+" |
                                secondLast === "*" |
                                secondLast === "/") {
                                    this.setState({
                                        // Generate a new array sliced from the original, minus the last two characters.
                                        display: [...tempDisplay.split("").slice(0, tempDisplay.split("").length - 2)].join("") + event.target.innerHTML
                                    });
                            // ...else tack on the negative sign.
                            } else {
                                this.setState({
                                    // Generate a new array sliced from the original, minus the last character.
                                    display: [...tempDisplay.split("").slice(0, tempDisplay.split("").length - 1)].join("") + event.target.innerHTML
                                });
                            }
                        } else {
                            this.setState({
                                display: tempDisplay + event.target.innerHTML
                            });
                        }
                    }
                    break;

                case "decimal":
                    if (this.state.display.match(/\./)) {
                        let numArr = this.state.display.split(/[+\-/*/]/);
                        if (numArr[numArr.length -1].match(/\./)) {
                            break;
                        } else {
                            this.setState({
                                display: tempDisplay + event.target.innerHTML
                            });
                            break;
                        }
                    } else {
                        this.setState({
                            display: tempDisplay + event.target.innerHTML
                        });
                        break;
                    }

                case "subtract":
                    if (lastCharacter === "-") {
                        break;
                    }
                    break;
                default:
                    if (tempDisplay === "0") {
                        this.setState({
                            display: event.target.innerHTML
                        });
                    } else {
                        this.setState({
                            display: tempDisplay + event.target.innerHTML
                        });
                    }
                    break;
            }
        } else {
            // Logic for pressing buttons with the previous result still on-screen.
            this.setState({
                evaluated: false
            });
            switch(event.target.id) {
                case "add":
                case "subtract":
                case "multiply":
                case "divide":
                    this.setState({
                        display: tempDisplay + event.target.innerHTML
                    });
                    break;

                case "clear":
                    this.setState({
                        display: "0"
                    });
                    break;

                case "equals":
                    this.setState({
                        evaluated: true
                    });
                    break;

                default:
                    this.setState({
                        display: event.target.innerHTML
                    });
                    break;
            }
        }

    }

    render() {
        return (
            <div className="calc-wrapper">
                <div className="calc-container">
                    <div className="calc-display" id="display">{this.state.display}</div>
                    <ButtonLayout clicker={this.handleClick} />
                </div>
            </div>
        );
    }
}


class ButtonLayout extends React.Component {
    render() {
        let buttonPopulator = buttons.map(button => (
            <Button id={button.button} key={button.button} value={button.value} clicker={this.props.clicker} />
        ));

        return (
            <div className="button-layout">{buttonPopulator}</div>
        );
    }
}


class Button extends React.Component {
    render() {

        return (
            <div id={this.props.id} className="calc-button" onClick={this.props.clicker} value={this.props.value}>{this.props.value}</div>
        );
    }
}


export default Calculator;