import React from 'react';
import { Route } from "react-router-dom";
import Home from './components/Home';
import RandomQuoteMachine from './components/RandomQuoteMachine';
import MarkdownPreviewer from './components/MarkdownPreviewer';
import Calculator from './components/Calculator';
import Pomodoro from './components/Pomodoro';

function App() {
  return (
    <div className="App">
      <Route exact={true} path="/" component={Home} />
      <Route exact={true} path="/random-quote-machine" component={RandomQuoteMachine} />
      <Route exact={true} path="/markdown-previewer" component={MarkdownPreviewer} />
      <Route exact={true} path="/calculator" component={Calculator} />
      <Route exact={true} path="/pomodoro-clock" component={Pomodoro} />
    </div>
  );
}

export default App;
