import React from "react";
import "./../style/quotes.scss";

class RandomQuoteMachine extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            text: "",
            author: "",
            quotes: []
        }
        this.updateQuote = this.updateQuote.bind(this);
    }

    componentDidMount() {
        fetch("https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json")
            .then(response => response.json())
            .then(
                (result) => {
                    // grab a random quote to plug into the state before the initial render
                    let randomQuote = result.quotes[Math.floor(Math.random() * result.quotes.length)];
                    this.setState({
                        text: randomQuote.quote,
                        author: randomQuote.author,
                        quotes: result.quotes
                    });
                }
        );
    }

    updateQuote() {
        let randomQuote = this.state.quotes[Math.floor(Math.random() * this.state.quotes.length)];
        this.setState({
            text: randomQuote.quote,
            author: randomQuote.author
        });
    }

    render() {
        let tweetLink = `http://twitter.com/intent/tweet?text=${this.state.text} %0a-${this.state.author}`;
        return (
            <div className="quote-container">
                <div id="quote-box">
                    <p id="text">"{this.state.text}"</p>
                    <p id="author">- {this.state.author}</p>
                    {/* <a id="new-quote" onClick={this.updateQuote}><i className="fas fa-plus"></i> New Quote</a> */}
                    <a id="tweet-quote" href={tweetLink} target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter"></i> Tweet Quote</a>
                    <button id="new-quote" onClick={this.updateQuote}><i className="fas fa-plus"></i> New Quote</button>
                </div>
            </div>
        )
    }
    
}

export default RandomQuoteMachine;